.recommendFollowItem {
  display: flex;
  width: 100%;
  height: 73px;
  padding: 16px;
  align-items: center;
  justify-content: space-between;
  .tweetInfo {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    margin-right: 13px;
    .avatar {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      cursor: pointer;
    }
    .tweetCreatorInfo {
      margin-left: 8px;
      cursor: pointer;
      .name {
        width: 70px;
        font-weight: 700;
        line-height: 26px;
        color: var(--dark-100);
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      .account {
        width: 70px;
        font-size: 14px;
        line-height: 22px;
        color: var(--secondary-color);
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }
  }
  .buttonContainer {
    transform: translateY(-2px);
  }
}
