.modal {
  position: absolute;
  z-index: 2;
  .backdrop {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .modal__container {
    height: 610px;
    overflow: hidden;
    position: absolute;
    top: 56px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
    width: 634px;
    background-color: #fff;
    border-radius: 14px;
    .top {
      width: 100%;
      border-bottom: solid 1px var(--border-color);
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 52px;
      padding: 8px 16px;
      .container {
        display: flex;
        .del__btn {
          width: 24px;
          height: 24px;
          content: url(../components/assets/icons/delBtn_active.svg);
          cursor: pointer;
        }
        .title {
          margin-left: 32px;
          color: #1c1c1c;
          font-weight: 700;
          font-size: 18px;
        }
      }
    }
    .cover__container {
      width: 100%;
      height: 200px;
      position: relative;
      overflow: hidden;
      .icons {
        position: absolute;
        z-index: 5;
        top: 50%;
        left: 50%;
        transform: translate(-50%);
        display: flex;
        img {
          margin: 0 16px;
          cursor: pointer;
        }
      }
      .backdrop {
        position: absolute;
      }
      .cover {
        width: 100%;
      }
    }
    .avatar__container {
      position: absolute;
      top: 179px;
      left: 16px;
      width: 140px;
      height: 140px;
      overflow: hidden;
      border-radius: 50%;
      border: solid 4px white;
      .upload {
        position: absolute;
        z-index: 5;
        top: 43%;
        left: 40%;
        cursor: pointer;
        label {
          all: unset;
          content: url(../components/assets/icons/camera.svg);
        }
        input {
          display: none;
        }
      }

      .backdrop {
        width: 100%;
        height: 100%;
        position: absolute;
      }
      .avatar {
        width: 140px;
        height: 140px;
      }
    }
  }
}
.auth__input__container {
  margin-top: 80px;
  padding: 0 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.upload {
  cursor: pointer;
  label {
    all: unset;
    content: url(../components/assets/icons/camera.svg);
  }
  input {
    display: none;
  }
}

.loadingMessage {
  color: var(--main-color);
  letter-spacing: 10px;
  font-size: 40px;
  width: 100%;
  height: 100%;
  border: solid 1px;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: loadingMessage 3s infinite;
}

@keyframes loadingMessage {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
